// app/catalog/products/page.tsx
import { Suspense } from 'react';
import ProductListPage from '@/components/ProductListPage';

const BACKEND_URL = process.env.BACKEND_URL || 'http://127.0.0.1:8000';

async function getProducts() {
  try {
    const response = await fetch(`${BACKEND_URL}/catalog/products/`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      cache: 'no-store',
    });

    if (!response.ok) {
      throw new Error('Failed to fetch products');
    }

    const data = await response.json();
    const products = (data.data?.results || data.results || []).map((product: any) => ({
      id: product.id,
      href: `/catalog/products/${product.id}`,
      imageSrc: product.images?.[0]?.url || '/placeholder.jpg',
      imageAlt: product.name,
      title: product.name,
      sizeLabel: product.available_sizes?.map((s: any) => s.name).join(' — ') || '',
      priceFormatted: `${product.sale_price || product.base_price} сом`,
      compareAtFormatted: product.sale_price ? `${product.base_price} сом` : undefined,
      rating: 4.5,
    }));

    return {
      products,
      total: data.data?.count || data.count || products.length,
    };
  } catch (error) {
    console.error('Error fetching products:', error);
    return { products: [], total: 0 };
  }
}

async function getFilters() {
  try {
    const response = await fetch(`${BACKEND_URL}/catalog/products/filters/`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      cache: 'no-store',
    });

    if (!response.ok) {
      throw new Error('Failed to fetch filters');
    }

    const data = await response.json();
    return data.data || data;
  } catch (error) {
    console.error('Error fetching filters:', error);
    return {
      categories: [],
      colors: [],
      sizes: [],
      priceRange: { min: 0, max: 100000 },
      seasons: [],
    };
  }
}

export default async function ProductsPage() {
  const [{ products, total }, filters] = await Promise.all([
    getProducts(),
    getFilters(),
  ]);

  return (
    <Suspense fallback={<div className="min-h-screen">Загрузка...</div>}>
      <ProductListPage
        initialProducts={products}
        filters={filters}
        totalCount={total}
      />
    </Suspense>
  );
}

export const metadata = {
  title: 'Каталог - Nely Look',
  description: 'Просмотрите нашу коллекцию одежды',
};